
.tableProfileArea{
    width: 100%;
    font-size: 16px;
}
.tableProfile {
    width: 98%;
    margin: auto;
}
.columnProfile3 {
    width: 50%;
}
.tableProfile th {
    text-align: left;
}
.tableProfile tr {
    background-color: white
}
.tableRowsProfile {
    height: 80px;
}
.tableRowsProfile:hover {
    background-color: #e5e4e4d1;
    color:#120fcb;
}
.imgContainerCell {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;          /* Add some padding */
    height: 100px; /* Increased from 80px to give more space */
    min-width: 120px; /* Add minimum width to prevent squeezing */
    cursor: pointer;
}

.adMainImage {
    max-width: 100%;
    max-height: 90px; /* Increased from 75px */
    width: auto; /* Let the width adjust proportionally */
    height: auto; /* Let the height adjust proportionally */
    object-fit: contain; /* Changed from cover to show whole image */
    border-radius: 5px;
  } 

.columnProfile2, .columnProfile3, .columnProfile4, .columnProfile5 {
    border-bottom: 2px double #979797;
}
.cellProfile2, .cellProfile3, .cellProfile4, .cellProfile5 {
    border-bottom: 2px dashed #c0c0c0;
    padding: 10px;
    cursor: pointer;
}
.cellProfile6 {
    border-bottom: 2px dashed #c0c0c0;
    padding: 1px;
    background-color: white !important;
    color: black !important;
    font-weight:initial !important; 
}
.profileListButtonsArea {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #beddf8;
    font-size: 14px;
    margin: 2px;
    padding: 5px;
    cursor: pointer;
}
.profileListButtonsArea:hover {
    font-weight: bolder;
    background-color: #6fb6f5;
}
.profileListIcons img {
    width: 22px;
}
.errorFieldProfile {
    font-size: 15px;
    font-style: italic;
    margin-left: 20px;
}
.noAdsMessage {
    margin-left: 20px;
}
.userInfoArea {
    margin-left: 20px;
    font-size: 17px;
    margin-bottom: 50px;
}
.userInfoArea div {
    margin-top: 8px;
}
.lastDivProfile {
    margin-bottom: 30px;
}
.welcomeMessageProfile {
    font-family: "Style Script", cursive;
    font-size: 33px;
    font-weight: 500;
    font-style: normal;
}
/* CSS */
.button-54 {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  letter-spacing: 2px;
  min-width: 155px;
  text-decoration: none;
  text-transform: uppercase;
  color: #000;
  cursor: pointer;
  border: 3px solid;
  padding: 0.25em 0.5em;
  box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px, 5px 5px 0px 0px;
  position: relative;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin-right: 20px;
}
.button-54:hover {
    background-color: #b0adad;
}
.button-54:active {
  box-shadow: 0px 0px 0px 0px;
  top: 5px;
  left: 5px; 
}
.heartIcon {
    width: 35px;
    cursor: pointer;
}
.likeArea {
    display: flex;
    align-items: center; /* vertical alignment */
    gap: 8px; /* horizontal spacing between items */
}

.ratingNum {
    border: 2px solid #cfde20;
    padding: 3px;
    margin-right: 15px;
    border-radius: 3px;
    min-width: 40px;
    display: inline-block; /* <-- this is key */
    text-align: center;     /* optional: centers number inside */
    user-select: none;
}
.raterParent {
    display: flex;
    align-items: center;
}
.reviewSmilingIcon {
    width: 25px;
}

@media (min-width: 768px) {
  .button-54 {
    padding: 0.25em 0.75em;
  }
}